.sliderWrapper{position:relative}.bg{position:absolute;height:100%;width:100%;background-color:#0f172a;top:0;left:0;z-index:1;opacity:1;visibility:visible;transition:all .3s}.bg_active{opacity:0;visibility:hidden;transition:all .3s}.item{height:calc(100vh - 70px);min-height:500px;position:relative}.image{height:100%;object-fit:cover;width:100%}.imageBg{position:absolute;top:0;left:0;right:0;bottom:0;z-index:1;opacity:.45;background-color:#0f172a}.content{position:absolute;top:50%;transform:translateY(-50%);color:#fff;width:100%;display:flex;justify-content:center;z-index:2}.contentWrapper{padding:0 100px}.title{font-family:var(--playfair-font),sans-serif;font-weight:bold;font-size:clamp(30px,4vw,58px);text-align:center;margin-bottom:25px;line-height:110%}.description{font-family:var(--playfair-font),sans-serif;font-weight:500;font-size:clamp(20px,2vw,28px);line-height:115%;text-align:center;padding:0 100px}.buttons{position:absolute;top:50%;transform:translateY(-50%);z-index:2;width:100%}.swiperPrev,.swiperNext{position:absolute;top:50%;transform:translateY(-50%);z-index:2;width:72px;height:72px;border-radius:72px;background-color:rgba(0,0,0,0);user-select:none;cursor:pointer;display:flex;align-items:center;justify-content:center;transition:all .3s}.swiperPrev:hover,.swiperNext:hover{background-color:rgba(0,0,0,.1);transition:all .3s}.swiperPrev:hover svg>path,.swiperNext:hover svg>path{fill-opacity:1;transition:all .3s}.swiperPrev svg>path,.swiperNext svg>path{transition:all .3s}.swiperPrev{left:max(40px,(100vw - 1400px)/2)}.swiperNext{right:max(40px,(100vw - 1400px)/2)}